<template>
  <section class="section" id="contact">
    <div class="container">
      <div class="section-heading">
        <h3 class="title is-2">contact</h3>
        <p>We'd love to hear from you</p>
      </div>
      <br />

      <div class="columns">
        <div class="column is-6 is-offset-3">
          <div class="box">
            <div class="field">
              <label class="label">Name</label>
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="name"
                  placeholder="Your name"
                />
              </div>
            </div>

            <div class="field">
              <label class="label">Email</label>
              <div class="control has-icons-left">
                <input
                  class="input"
                  type="email"
                  placeholder="Your email"
                  v-model="email"
                />
                <span class="icon is-small is-left">
                  <i class="fas fa-envelope"></i>
                </span>
              </div>
            </div>

            <div class="field">
              <label class="label">Message</label>
              <div class="control">
                <textarea
                  class="textarea"
                  placeholder="Your message"
                  v-model="message"
                ></textarea>
              </div>
            </div>
          </div>
        </div>
      </div>

      <button @click="sendMessage" :class="buttonType">
        <span class="icon">
          <i :class="iconType"></i>
        </span>
        <span>{{ buttonText }}</span>
      </button>
      <p class="warning" v-if="incomplete">Please complete all fields</p>
    </div>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "Contact",
  props: {},
  data: function() {
    return {
      name: "",
      email: "",
      message: "",
      incomplete: false,
      buttonText: "Submit",
      iconType: "fas fa-envelope",
      buttonType: "button is-centered is-link is-large"
    };
  },
  methods: {
    sendMessage() {
      // checks that the contact form has been completed
      if (
        this.name.length === 0 ||
        this.email.length === 0 ||
        this.message.length === 0
      ) {
        this.incomplete = true;
      } else {
        axios({
          method: "post",
          //  url: "http://localhost:8000/contact-form",
          url: "https://storybook-backend.herokuapp.com/contact-form",
          data: {
            name: this.name,
            email: this.email,
            message: this.message
          }
        });
        this.incomplete = false;
        // changes button message and colour to show mesage received
        this.buttonText = "Message received";
        this.iconType = "fas fa-check";
        this.buttonType = "button is-centered is-link is-large is-success";

        // reverts button back to "Submit" after 3 seconds
        setTimeout(this.revertButton, 3000);
      }
    },
    revertButton() {
      this.buttonText = "Submit";
      this.buttonType = "button is-centered is-link is-large";
      this.iconType = "fas fa-envelope";
      // clears form
      this.name = "";
      this.email = "";
      this.message = "";
    }
  }
};
</script>

